import React from 'react'
import {scaleLinear} from 'd3-scale'
import {line} from 'd3-shape'
import {extent} from 'd3-array'

import './XYPlot.css'


const XYPlot = ({
        data = [],
        x = el => el.x,
        y= el => el.y,
        color = 'black',
        opts = {}
    }) => {
        const xScale = scaleLinear().range([0,400]).domain(extent(data, x))
        const yScale = scaleLinear().range([100,0]).domain(extent(data, y))
        const xWrap = (el) => xScale(x(el))
        const yWrap = (el) => yScale(y(el))

        return (
            <svg viewBox={opts.viewBox || "-10 -1 410 110"}>
                <path stroke='black' d={line().x(xWrap).y(yWrap)(data)} style={{stroke: color}}/>
            </svg>
        )
}

export default XYPlot
