import React from 'react';
import Subscribe from './Subscription';
import XYPlot from './XYPlot';
import TimeSeries from './TimeSeries';
import Line from './Line';
import JoyPlot from './JoystickPlot';
import RotationPlot from './RotationPlot';
import HasPiece from './HasPiece';
import BigNumber from './BigNumber';
const VisBoard = ({topics}) => {

return(
<React.Fragment>
    {topics.map((param, i) => {
      switch(param.visType){
      case "TimeSeries": {
      return(
      <div className="col-half col">
        <Subscribe type={param.type} topic = {param.topic}>
            <TimeSeries x ={param.x}  size={param.size}>
              <Line y = {param.y} color={param.color}/>
            </TimeSeries>
        </Subscribe>
      </div>
    )
  }
    case "JoystickPlot":
    {
      return(
      <div className="square-col-half nogreycol">
      <Subscribe type={param.type} topic = {param.topic}>
          <JoyPlot x={param.x} y={param.y} color = {param.color}/>
      </Subscribe>
      </div>
    )
   }
   case "XYPlot":
   {
     return(
     <div className="square-col-half col">
     <Subscribe type={param.type} topic = {param.topic}>
         <XYPlot x={param.x} y={param.y} color = {param.color}/>
     </Subscribe>
     </div>
   )
  }
  case "RotationPlot":
  {
    return(
    <div className="square-col-half nogreycol">
    <Subscribe type={param.type} topic = {param.topic}>
        <RotationPlot x={param.x} y={param.y} color = {param.color}/>
    </Subscribe>
    </div>
  )
 }
 case "HasPiece":
 {
   return(
   <div className="square-col-half nogreycol">
   <Subscribe type={param.type} topic = {param.topic}>
       <HasPiece sensor= {param.sensor}/>
   </Subscribe>
   </div>
 )
}
case "BigNumber":
{
  return(
  <div className="square-col-half nogreycol">
  <Subscribe type={param.type} topic = {param.topic}>
      <BigNumber number = {param.number}/>
  </Subscribe>
  </div>
)
}
 }
 })



}

 </React.Fragment>

);

}

export default VisBoard
