import React from 'react'
import ROSLIB, { Topic } from 'roslib'

// This is the object that actually handles subscriptions
class Context extends Object {
    client = new ROSLIB.Ros({})
    topics = {}
    subscribe = (name, messageType, callback) => {
        // If we already have a subscription to this topic update the callback rather than subbing again
        // Currently can't have two subscribers to same topic...
        if (this.topics[name]) {
            this.topics[name].subscribe((msg) => callback(msg))
            return
        }
        this.topics[name] = new Topic({
            ros: this.client,
            name,
            messageType
        })
        this.topics[name].subscribe((msg) => callback(msg))
    }
    removeSubscription = (name) => {
        const topic = this.topics[name]
        topic.unsubscribe()
        delete this.topics[name]
    }
}


// And this let's us expose it via `useContext`
export const ROSContext = React.createContext(
    {
        ros: new Context()
    }
);
