import React from 'react'
import {scaleLinear} from 'd3-scale'
import {line} from 'd3-shape'
import {extent} from 'd3-array'
const Line = ({
        data = [],
        y = el => el.y,
        color = 'black',
        x
    }) => {
        const yScale = scaleLinear().range([0,100]).domain(extent(data, y))
        const yWrap = (el) => yScale(y(el))
        return (
            <path style={{stroke: color}} d={line().x(x).y(yWrap)(data)} />
        )
}

export default Line
