import React, { useContext } from 'react'
import { useList } from 'react-hooks-lib'
import { ROSContext } from '../ros/context'

const Service = ({topic, type, children}) => {
    const { ros } = useContext(ROSContext)
    // This creates a list of datapoints
    // list:data renames the list key to data
    return React.Children.map(children, child => {
        return React.cloneElement(child, {ros: ros.client})
    })

}
export default Service
