import React, { useContext, useState } from 'react'
import { ROSContext } from './context'

// This is just a gnarly mess of managing connection state
export default () => {
    const {ros} = useContext(ROSContext)
    const [isConnected, changeConnected] = useState(false)
    const [isConnecting, changeIsConnecting] = useState(false)
    const [address, changeAddress] = useState('ws://10.9.0.8:5803')
    // const

    if(isConnected) {
        return <button onClick={() => {
            console.log('discon')
            ros.client.close()
        }}>Disconnect from {address}</button>
    }
    if(isConnecting) {
        console.log('connecting...')
        return `Connecting to ${address}...`
    }
    ros.client.on('connection', () => {
        changeIsConnecting(false)
        changeConnected(true)
    })

    ros.client.on('close', () => {
        changeIsConnecting(false)
        changeConnected(false)
    })

    // ros.on('error', () => {
    //     if(isConnected) {
    //         changeConnected(false)
    //     }
    // })

    const onAddressChange = (e) => {
        changeAddress(e.target.value)
    }

    const onConnect = () => {
        changeIsConnecting(true)
        ros.client.connect(address)
    }

    return (
        <div>
            <input value={address} onChange={onAddressChange} />
            <button onClick={onConnect} >Connect</button>
        </div>
    )
}
