import { useState, useEffect } from 'react'


export default (funcOnce, ...args) => {
    const [hasCalled, setCalled] = useState(false)
    const [error, setError] = useState(null)
    const [data, setData] = useState(null)
    // We will call this ONCE
    if(!hasCalled){
        try {
            setCalled(true)
            setData(funcOnce(...args))
        } catch (error) {
            console.error(error)
            // If it errors out we set the error object
            setError(error)
        }
    }
    return {
        hasCalled,
        error,
        data
    }   
}