import React from 'react'
import {scaleLinear} from 'd3-scale'
import {line} from 'd3-shape'
import {extent} from 'd3-array'

import './JoystickPlot.css'

const BigNumber = ({
        data = [],
        number = el => el.number,
        opts = {}
    }) => {

        if(data[data.length -1]===undefined){
        return (
        <svg viewBox={opts.viewBox || "-10 -10 20 20"}>
        <text x="0" y="10">-1</text>
        </svg>
      )
    }else if(number(data[data.length-1])==0){
        return (
            <svg viewBox={opts.viewBox || "-10 -10 20 20"}>
            <text x="0" y="8">{number(data[data.length-1])}</text>
            </svg>
        )
}
}

export default BigNumber
