import React from 'react'
import {scaleLinear} from 'd3-scale'
import {line} from 'd3-shape'
import {extent} from 'd3-array'

import './JoystickPlot.css'

const RotationPlot = ({
        data = [],
        x = el => el.x,
        y = el => el.y,
        color = 'darkred',
        opts = {}
    }) => {
        const xScale = scaleLinear().range([0,3]).domain([-3,3])
        const xWrap = (el) => x(el)
        if(data[data.length-1]===undefined){
        return (
        <svg viewBox={opts.viewBox || "-1 -1 2 2"}>
        </svg>
      )
      }
        return (
        <svg viewBox={opts.viewBox || "-3 -3 6 6"}>
        <line x0='0' y0='0' x1={3*(Math.cos(xWrap(data[data.length-1])+1.57))} y1={3*(Math.sin(xWrap(data[data.length -1])-1.57))} stroke='black' strokeWidth='.1'/>
        </svg>
      )


}

export default RotationPlot
