import React from 'react'
import {scaleLinear} from 'd3-scale'
import {line} from 'd3-shape'
import {extent} from 'd3-array'
import Line from './Line.js'
import './TimeSeries.css'

const TimeSeries = ({
        data = [],
        x = el => el.x,
        opts = {},
        children,
        size=10000
    }) => {
        const xScale = scaleLinear().range([0,400]).domain(extent(data.slice(data.length-size,data.length), x))
        const xWrap = (el) => xScale(x(el))
        return (
            <svg viewBox={opts.viewBox || "-10 -1 410 110"}>
                {React.Children.map(children, (child,i) => {
                    return React.cloneElement(child, {data: data.slice(data.length-size,data.length), x:xWrap})
            })}
            </svg>
        )
}

export default TimeSeries
