import React from 'react'
import {scaleLinear} from 'd3-scale'
import {line} from 'd3-shape'
import {extent} from 'd3-array'

import './JoystickPlot.css'

const JoyPlot = ({
        data = [],
        x = el => el.x,
        y= el => el.y,
        color = 'darkred',
        opts = {}
    }) => {
        const xScale = scaleLinear().range([-10,10]).domain([-10,10])
        const yScale = scaleLinear().range([-10,10]).domain([-10,10])
        const xWrap = (el) => xScale(x(el))
        const yWrap = (el) => yScale(y(el))

        if(data[data.length -1]===undefined){
        return (
        <svg viewBox={opts.viewBox || "-10 -10 20 20"}>
        <rect  x='-40%' y='-40%' width="80%" height="80%" strokeWidth='.1' stroke='black' fill='none'/>
        </svg>
      )
      }
        return (
            <svg viewBox={opts.viewBox || "-10 -10 20 20"}>
            <rect  x='-40%' y='-40%' width="80%" height="80%" strokeWidth='.1' stroke='black' fill='none'/>
            <circle cx = {8*xWrap(data[data.length -1])} cy = {8*yWrap(data[data.length -1])} r ={1.3} style={{fill: color}}/>
            </svg>
        )

}

export default JoyPlot
