import React, { useContext } from 'react'
import { useList } from 'react-hooks-lib'
import { ROSContext } from '../ros/context'
import useOnce from './useOnce'

const Subscription = ({topic, type, children}) => {
    const { ros } = useContext(ROSContext)
    // This creates a list of datapoints
    // list:data renames the list key to data
    const { list: data, push } = useList()
    if (topic) {
        useOnce(() => {
            ros.subscribe(topic, type, push)
        })
    }
    return React.Children.map(children, child => {
        return React.cloneElement(child, {data})
    })

}

export default Subscription
