import ROSLIB from 'roslib'
import React, { useContext } from 'react'
import { useList } from 'react-hooks-lib'
// import { ROSContext } from '../ros/context'


const ServiceCallGyro = ({ros}) => {

const fieldOrient = () => {

  var addTwoIntsClient = new ROSLIB.Service({
        ros : ros,
        name : '/frcrobot_rio/navx_offset_controller/navX_offset_service',
        serviceType : 'navx_offset_controller/NavXSrv'
    });

      var request = new ROSLIB.ServiceRequest({
        value : -10000
    });

      addTwoIntsClient.callService(request, function(result) {
        console.log('fieldOrient') });

}
const constantZero = () => {

  var addTwoIntsClient = new ROSLIB.Service({
        ros : ros,
        name : '/frcrobot_rio/navx_offset_controller/navX_offset_service',
        serviceType : 'navx_offset_controller/NavXSrv'
    });

      var request = new ROSLIB.ServiceRequest({
        value : 0
    });
      addTwoIntsClient.callService(request, function(result) {
        console.log('constantZero') });

}
const zeroinFieldOrient = () => {

  constantZero()
  constantZero()
  constantZero()
  setTimeout(fieldOrient(),5000)
  setTimeout(fieldOrient(),5000)
  setTimeout(fieldOrient(),5000)
}
return(
  <div>
  <button onClick={constantZero}>
    Robot Orient
  </button>
  <button onClick={fieldOrient}>
    Field Orient
  </button>
  <button onClick={zeroinFieldOrient}>
    Zero in Field Orient
  </button>
  </div>
)
}

export default ServiceCallGyro;
